import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CatalogueComponent } from './components/pages/catalogue/catalogue.component';
import { InformationComponent } from './components/pages/information/information.component';

const routes: Routes = [
  {
    path: "", redirectTo: "/catalogue", pathMatch: "full"
  },
  {
    path: "information", component: InformationComponent
  },
  {
    path: "catalogue", component: CatalogueComponent
  },
  {
    path: "admin", loadChildren: () => import("./components/pages/admin/admin.module").then(m => m.AdminModule)
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
